body {
	font-family: "acherusgrotesque-regular", sans-serif;
	color: #4A4A4A;
	line-height: 24px;
	font-style: normal;
	min-height: 100%;
	background-color: #F8F9F9;
	letter-spacing: .025em;
}

h1, h2, h3, h4, h5, h6 {
	font-family: "acherusgrotesque-medium", sans-serif;
	font-weight: normal;
	color: #1C222E;
	text-rendering: optimizelegibility;
	margin-bottom: 24px;
	line-height: 24px;
}

h1 {
	font-size: 72px;
	font-size: 4.5rem;
	line-height: 1;
}

h2 {
	font-size: 60px;
	font-size: 3.75rem;
	line-height: 1.2;
}

h2.entry__title {
	font-size: 20px;
	font-size: 1.25rem;
	line-height: 1.2;
}

h3 {
	font-size: 48px;
	font-size: 3rem;
	line-height: 1;
}

h4 {
	font-size: 32px;
	font-size: 2rem;
	line-height: 1.5;
}

h5 {
	font-size: 24px;
	font-size: 1.5rem;
	line-height: 1;
}

h6 {
	font-size: 20px;
	font-size: 1.25rem;
	line-height: 1.2;
}

p {
	margin-bottom: 24px;
}

blockquote {
	quotes: "“" "”";
}

blockquote p {
	text-indent: -0.42em;
}

blockquote p:before {
	content: open-quote;
}

blockquote p:after {
	content: no-close-quote;
}

blockquote p:last-of-type {
	margin-bottom: 0;
}

blockquote p:last-of-type:after {
	content: close-quote;
}

table {
	max-width: 100%;
	background-color: white;
	border-collapse: collapse;
	border-spacing: 0;
}

.app-content {
	padding: 120px 0;
}

.fl-builder .app-content,
.woocommerce-page .app-content {
	max-width: 100%;
	margin: 0;
	padding: 59px 0 0;
}

.has-sidebar .app-content {
	display: flex;
	flex-flow: row wrap;
	justify-content: space-between;
}

.has-sidebar .app-content .site-main {
	flex: 0 1 70%;
}

.has-sidebar .app-content .sidebar {
	flex: 0 1 27.5%;
}

.post-list__card {
	background-color: white;
	border-radius: 3px;
	box-shadow: 0 1px 3px 0 rgba(53, 69, 88, 0.2);
	padding: 24px;
	overflow: hidden;
}

.post-list__card img {
	margin-right: -24px;
	margin-left: -24px;
	margin-top: -24px;
	margin-bottom: 24px;
	max-width: calc(100% + 52.8px);
}

.post-list__card .entry__summary p:last-child {
	margin-bottom: 0;
}

img {
	max-width: 100%;
	height: auto;
	display: block;
	position: relative;
}

a {
	color: #2DA3ED;
	text-decoration: none;
	transition: all .2s ease-in-out;
}

a:hover {
	color: #107bbd;
	text-decoration: none;
}

h2.entry__title a {
	color: #1C222E;
}

.entry__more-link {
	color: #4A4A4A;
}

.app-header__nav {
	display: flex;
	align-items: center;
}

.app-header__nav svg {
	position: relative;
	top: 2px;
	margin: 0 6px;
}

.menu__items {
	list-style: none;
	display: flex;
	margin: 0;
	padding: 0;
	font-family: "acherusgrotesque-medium", sans-serif;
}

.menu__item {
	position: relative;
	font-size: 15px;
	font-size: 0.9375rem;
	line-height: 1.6;
}

.menu__item:hover > ul {
	z-index: 0;
	visibility: visible;
	opacity: 1;
}

.menu__link {
	color: #1C222E;
	padding: 12px;
}

.menu__link:hover {
	color: #2DA3ED;
}

.menu__sub-menu {
	list-style: none;
	display: flex;
	flex-flow: column;
	margin: 0;
}

@media (min-width: 600px) {
	.menu__sub-menu {
		opacity: 0;
		visibility: hidden;
		position: absolute;
		background-color: white;
		top: 24px;
		width: 200px;
		padding-bottom: 12px;
		left: -10px;
		padding-top: 24px;
		box-shadow: 0 20px 25px 0 rgba(28, 34, 46, 0.08);
		transition: all .3s ease-in-out;
	}
}

.menu__sub-menu .menu__item {
	padding: 8px;
}

.pagination__items {
	list-style: none;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: center;
}

.pagination__item {
	flex: 0 1 auto;
}

/*!
 * Toggle Menu
 * @description Tasty CSS-animated toggle-menus
 * @author Jonathan Suh @jonsuh
 * @site https://jonsuh.com/toggle-menus
 * @link https://github.com/jonsuh/toggle-menus
 */
.toggle-menu {
	display: flex;
	cursor: pointer;
	transition-property: opacity, filter;
	transition-duration: 0.15s;
	transition-timing-function: linear;
	font: inherit;
	color: inherit;
	text-transform: none;
	background-color: transparent;
	border: 0;
	margin: 0;
	overflow: visible;
	margin-right: 24px;
}

@media (min-width: 600px) {
	.toggle-menu {
		display: none;
	}
}

.nav-open .toggle-menu:before {
	color: #2DA3ED;
}

.toggle-menu:before {
	content: "Menu";
	text-transform: uppercase;
	font-size: 15px;
	font-size: 0.9375rem;
	line-height: 1.6;
	padding-right: 10px;
	color: #2DA3ED;
}

.toggle-menu:focus {
	outline: none;
}

.toggle-menu-box {
	width: 30px;
	height: 24px;
	display: inline-block;
	position: relative;
}

.toggle-menu-inner, .toggle-menu-inner::before, .toggle-menu-inner::after {
	width: 30px;
	height: 3px;
	background-color: #2DA3ED;
	border-radius: 4px;
	position: absolute;
	transition-property: transform;
	transition-duration: 0.15s;
	transition-timing-function: ease;
}

.nav-open .toggle-menu-inner, .nav-open .toggle-menu-inner::before, .nav-open .toggle-menu-inner::after {
	background-color: #a00;
}

.toggle-menu-inner {
	display: block;
	top: 50%;
	margin-top: -2px;
	transition-duration: 0.22s;
	transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.nav-open .toggle-menu-inner {
	transform: rotate(225deg);
	transition-delay: 0.12s;
	transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}

.toggle-menu-inner::before {
	content: "";
	display: block;
	top: -7.5px;
	transition: top 0.1s 0.25s ease-in, opacity 0.1s ease-in;
}

.nav-open .toggle-menu-inner::before {
	top: 0;
	opacity: 0;
	transition: top 0.1s ease-out, opacity 0.1s 0.12s ease-out;
}

.toggle-menu-inner::after {
	content: "";
	display: block;
	bottom: -7.5px;
	transition: bottom 0.1s 0.25s ease-in, transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.nav-open .toggle-menu-inner::after {
	bottom: 0;
	transform: rotate(-90deg);
	transition: bottom 0.1s ease-out, transform 0.22s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.wp-block-audio {
	margin: 0;
}

.wp-block-audio audio {
	width: 100%;
}

.wp-block-button {
	margin-bottom: 1.5em;
}

